import * as React from "react"
import { graphql } from "gatsby"
import { Container, Section, Text, SuperHeading } from "./ui"
import * as styles from "./about-hero.css"
import Image from 'rc-image';


export default function AboutHero(props) {
  return (
    <Section>
      <Container>
        <SuperHeading className={styles.aboutHeroHeader}>
          {props.heading}
        </SuperHeading>
        {props.description && (
          <Text className={styles.aboutHeroText}>{props.description}</Text>
        )}
      </Container>
      <Container width="wide" className={styles.aboutBody}>
        <div>
          {props.image && (
            <Image
              alt={props.image.alt}
              src={'https://s3.bmp.ovh/imgs/2022/04/20/ee5c027376798e82.jpg'}
              className={styles.aboutHeroImage}
            />
          )}
        </div>
         <div
            dangerouslySetInnerHTML={{
              __html: props.body,
            }}
            className={styles.aboutBodyContent}
          />
      </Container>
    </Section>
  )
}

export const query = graphql`
  fragment AboutHeroContent on AboutHero {
    id
    heading
    description {
      body
    }
    finalDes {
      alt
    }
    body
    image {
      id
      gatsbyImageData
      alt
    }
  }
`
